.loader {
  width: 20em;
  height: 20em;
  z-index: 1001;
  opacity: 1;
  visibility: visible;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  display: flex;
  position: relative;
}

.loader.done {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.loader .face {
  border-style: solid;
  border-radius: 50%;
  animation: animate 3s linear infinite;
  position: absolute;
}

.loader .face:nth-child(1) {
  width: 100%;
  height: 100%;
  color: #0064ff;
  --deg: -45deg;
  border-width: .2em .2em 0 0;
  border-color: currentColor rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) currentColor;
  animation-direction: normal;
}

.loader .face:nth-child(2) {
  width: 70%;
  height: 70%;
  color: gold;
  --deg: -135deg;
  border-width: .2em 0 0 .2em;
  border-color: currentColor currentColor rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  animation-direction: reverse;
}

.loader .face .circle {
  width: 50%;
  height: .1em;
  transform: rotate(var(--deg) );
  transform-origin: 0;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 50%;
  left: 50%;
}

.loader .face .circle:before {
  content: "";
  width: 1em;
  height: 1em;
  background-color: currentColor;
  border-radius: 50%;
  position: absolute;
  top: -.5em;
  right: -.5em;
  box-shadow: 0 0 2em, 0 0 4em, 0 0 6em, 0 0 8em, 0 0 10em, 0 0 0 .5em rgba(255, 255, 0, .1);
}

