.footer {
    display: flex;
    justify-content: center;
    align-items: center;

    min-height: 77px;

    background-color: getColor('footer-bgr');
}

.footer__box {
    @include mq($width-tablet) {
        display: flex;
        justify-content: center;
    }
}

.footer__text {
    display: flex;
    align-items: center;
    text-align: center;

    margin-left: 5px;

    font-size: 16px;
    font-weight: 400;
    color: getColor('footer-text');

    @include mq($until: $width-tablet) {
        justify-content: center;
        font-size: 14px;
        line-height: 1.4;
    }
}

.footer__icon {
    display: flex;
    align-items: center;
    color: getColor('modal-accent');
    margin: 0 6px;

    font-size: 19px;
    line-height: 1.4;

    animation: 'anime' 300ms infinite alternate ease-in;
}

.footer__btn {
        cursor: pointer;
        border: none;
        background-color: getColor('footer-bgr');
        color: getColor('footer-text');
        text-decoration: underline;
        transition: color $duration $timing-function;
    
        &:hover,
        &:focus {
            color: getColor('modal-accent');
        }
}